import React from 'react';
import styled from 'styled-components';
const Loading = () => {
  return (
    <LoaderWrapper>
      <div></div>
      <div></div>
    </LoaderWrapper>
  );
};
const LoaderWrapper = styled.div`
  display: inline-block;
  position: absolute;
  width: 80px;
  background-color: transparent;
  z-index: 700;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 80px;

  div {
    position: absolute;
    border: 4px solid #fff;
    opacity: 1;
    border-radius: 50%;
    animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  }
  div:nth-child(2) {
    animation-delay: -0.5s;
  }
  @keyframes lds-ripple {
    0% {
      top: 36px;
      left: 36px;
      width: 0;
      height: 0;
      opacity: 1;
    }
    100% {
      top: 0px;
      left: 0px;
      width: 72px;
      height: 72px;
      opacity: 0;
    }
  }
`;
export default Loading;
